.post{
  background-color: white;
  max-width: 100%;
  border: 1px solid lightgray;
  margin-bottom: 45px;
}
.post_image{
  width: 100%;
  height: 500px;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}
.post_text{
  font-weight: normal;
  padding: 20px;
}
.post_header{
  display: flex;
  align-items: center;
  padding: 20px;
}
.post_avatar{
  margin-right: 10px;
}
